.development {
    background-color: #ff6943;
    font-family: 'Open Sans', sans-serif;
}

.development .container {
    padding: 3rem 5rem;
}

.development .container .row {}

.development .container .row {}

.development .container .row .col-md-12 {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 2rem;
}

.development .container .row .col-md-12 .offer {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
}

.development .container .row .col-md-12 .offer h2.head-2 {
    font-size: 3rem;
    line-height: 1.1;
    margin-top: 10px;
    margin-bottom: 20px;
    font-weight: 700;
    color: #fff;
}

.development .container .row .col-md-12 .offer p.para {
    text-align: left !important;
    font-size: 1.25rem;
    line-height: 1.8;
    margin: 0 auto 0 0;
    color: #fff;
}

.development .container .row .col-md-12 .offer button {
    font-family: 'Open Sans', sans-serif;
    background-image: none;
    border-style: none;
    letter-spacing: 2px;
    text-transform: uppercase;
    font-weight: 700;
    font-size: 1rem;
    margin: 30px auto 0 0;
    padding: 18px 50px 20px 49px;
    border-radius: 5px;
    background-color: white;
    color: #ff6943;
    transition: all 150ms linear;
    float: left;
}

.development .container .row .col-md-12 .offer button:hover {
    background-color: #ff967b;
    color: #fff;
}



.development .container .row .col-md-12 .cards {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 1.5rem;
}

.development .container .row .col-md-12 .cards .card {
    background-color: #fff;
    display: grid;
    grid-template-columns: 130px 250px;
    gap: 1rem;
    padding: 2rem 0rem 2rem 8px;
    width: 28.5rem;
    border-radius: 0 !important;
}

.development .container .row .col-md-12 .cards .card i {
    font-size: 2.25rem;
    background: #ff6943;
    width: 80px;
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    border-radius: 50px;
    margin: 30% auto;
}

.development .container .row .col-md-12 .cards .card .content {}

.development .container .row .col-md-12 .cards .card .content h5.head-5 {
    font-size: 1.25rem;
    line-height: 1.2;
    margin-top: 20px;
    margin-bottom: 20px;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 2px;
    font-family: Montserrat, sans-serif;
}

.development .container .row .col-md-12 .cards .card .content p.paragraph {
    margin-top: 20px;
    margin-bottom: 20px;
    font-size: 1.125rem;
    line-height: 1.8;
    color: #464343;
    font-weight: 500;
}



@media only screen and (max-width:767px) {
    .development .container {
        padding: 3rem 0;
    }

    .development .container .row .col-md-12 .cards .card {
        width: auto;
    }
}

@media only screen and (max-width:600px) {
    .development .container .row .col-md-12 .cards {
        grid-template-columns: repeat(auto-fit, minmax(259px, 1fr));
    }

    .development .container .row .col-md-12 {
        grid-template-columns: repeat(auto-fit, minmax(255px, 1fr));
    }

    .development .container {
        padding: 3rem 2rem;
    }
}

@media only screen and (max-width:500px) {
    .development .container .row .col-md-12 .cards .card {
        grid-template-columns: 100px auto;
        padding: 2rem 1rem 2rem 16px;
    }

    .development .container .row .col-md-12 .offer p.para {
        line-height: 1.4;
    }

    .development .container .row .col-md-12 .offer h2.head-2 {
        font-size: 2.3rem;
    }
}

@media only screen and (max-width:420px) {
    .development .container .row .col-md-12 .cards .card {
        grid-template-columns: 80px auto;
        padding: 1rem;
    }

    .development .container .row .col-md-12 .cards .card .content h5.head-5 {
        margin-top: 7px;
        margin-bottom: 7px;
    }

    .development .container .row .col-md-12 .cards .card .content p.paragraph {
        margin-top: 7px;
        margin-bottom: 7px;
        line-height: 1.5;
    }

    .development .container .row .col-md-12 .cards .card i {
        margin: 0 auto;
    }

    .development .container .row .col-md-12 .cards {
        gap: 10px;
    }

    .development .container {
        padding: 1rem 1rem 2rem;
    }
}
