@import url('https://fonts.googleapis.com/css2?family=Merriweather:wght@300;400;700;900&family=Metrophobic&family=Open+Sans:wght@300;400;500;600;700;800&family=Roboto:wght@100;300;400;500;700;900&display=swap');

@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600;700;800;900&display=swap');

.portfolio {
    font-family: Montserrat, sans-serif;
}

.portfolio h2.head-2 {
    font-size: 3rem;
    line-height: 1.1;
    margin-top: 20px;
    margin-bottom: 20px;
    font-weight: 700;
}

.portfolio p.para {
    font-size: 1.125rem;
    word-wrap: break-word;
    position: relative;
    text-align: center;
    font-weight: 600;
    width: 810px;
}

.portfolio p.para a {
    text-decoration: none;
}

.portfolio .card-item {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
    grid-gap: 2rem;
}

.card-items {
    border-radius: 40px;
    height: 85%;
    border-radius: 40px;
}

.card-items img {
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
    height: 100%;
    width: 100%;
}

.card-items h5.head-5 {
    background: white;
    color: black;
    padding: 1.5rem 1rem;
    font-weight: 700;
    text-transform: capitalize;
    border-bottom-left-radius: 20px;
    border-bottom-right-radius: 20px;
}


@media only screen and (max-width:1000px) {
    .portfolio p.para {
        width: 100%;
    }
}

@media only screen and (max-width:575px) {
    .portfolio {
        padding: 0 2rem;
    }
}

@media only screen and (max-width:500px) {
    .portfolio .card-item {
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
        grid-gap: 2rem;
    }
}

@media only screen and (max-width:420px) {
    .portfolio {
        padding: 0 1rem;
    }
}