
.julius {
    background-color: rgb(242, 242, 242);
}

.julius .container {
    font-family: Montserrat,sans-serif;
    padding: 3rem;
}

.julius .container .row {}

.julius .container .row .col-md-12 {
    display: grid;
    grid-template-columns: repeat(auto-fit,minmax(450px,1fr));
    grid-gap: 1rem;
}

.julius .container .row .col-md-12 .card {
    padding: 13px 1rem 1rem;
    background-color: transparent;
    /* border: none; */
}

.julius .container .row .col-md-12 .card h2.head-2 {
    font-size: 3rem;
    line-height: 1.1;
    padding-bottom: 0;
    font-family: Montserrat,sans-serif;
    font-weight: 700;
}

.julius .container .row .col-md-12 .card .email-id {
        font-size: 0.875rem;
        text-transform: uppercase;
        font-weight: 500;
        letter-spacing: 1px;
        margin: 5px auto 0 0;
        padding: 0;
        color: #111;
        user-select: none;
}

.julius .container .row .col-md-12 .card .email-id:hover {
    color: orangered;
}

.julius .container .row .col-md-12 .card .address {
    margin-top: 20px;
    margin-bottom: 20px;
    font-size: 1.125rem;
    line-height: 1.8;
    color: #111;
}

.julius .container .row .col-md-12 .card button {
    letter-spacing: 1px;
    font-size: 0.75rem;
    background-image: none;
    --radius: 6px;
    text-transform: uppercase;
    font-weight: 600;
    margin: 20px auto 0 0;
    padding: 10px 21px 10px 20px;
    border-radius: 7px;
    border: none;
    background-color: #b3b3b3 !important;
    color: #fff;
    transition: all 100ms linear;
}

.julius .container .row .col-md-12 .card button:hover {
    background-color: #000 !important;
}


@media only screen and (max-width:1199px) {
    .julius .container .row .col-md-12 {
        grid-template-columns: repeat(auto-fit,minmax(278px,1fr));
    }
}

@media only screen and (max-width:991px) {
    .julius .container .row .col-md-12 {
        grid-template-columns: repeat(auto-fit,minmax(278px,1fr));
    }
}

@media only screen and (max-width:767px) {
    .julius .container .row .col-md-12 {
        grid-template-columns: repeat(auto-fit,minmax(278px,1fr));
    }

    .julius .container {
        padding: 3rem 1rem;
    }
}

@media only screen and (max-width:500px) {
    .julius .container {
        padding: 1rem 1rem 0;
    }
    .form-container {
        padding: 0 !important;
    }
}

@media only screen and (max-width:360px) {
    .julius .container .row .col-md-12 {
        grid-template-columns: repeat(auto-fit,minmax(160px,1fr));
    }
}