.meaning-full {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
    grid-gap: 2rem;
    padding: 0 6.2rem 7rem;
}

.meaning-full img.man-image {
    width: 100%;
    height: 560px;
    object-fit: cover;
}

.meaning-full .meaning {
    padding: 1rem;
    padding-top: 0;
}

.meaning-full .meaning h6.head-6 {
    font-size: 1.125rem;
    line-height: 1.2;
    margin-top: 20px;
    margin-bottom: 20px;
    font-weight: 700;
    text-transform: uppercase;
    font-weight: 500;
    letter-spacing: 2px;
    margin: 20px 163px 0 0;
}

.meaning-full .meaning h2.head-2 {
    font-size: 3.75rem;
    line-height: 1.1;
    margin-top: 20px;
    margin-bottom: 20px;
    list-icon-spacing: 0.3;
    list-icon-size: 0.8;
    font-family: Montserrat, sans-serif;
    font-weight: 700;
}

.meaning-full .meaning p.para {
    margin-top: 20px;
    margin-bottom: 20px;
    font-size: 1.25rem;
    line-height: 1.8;
    font-family: 'Open Sans', sans-serif;
}
.meaning-full .meaning p.para a {
    font-weight: 600;
    text-decoration: none;
}

.meaning-full .meaning hr {}

.meaning-full .meaning .smith {
    /* display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap; */
}

.meaning-full .meaning .smith img {
    width: 70px;
    position: relative;
    top: 13px;
}

.meaning-full .meaning .smith p.paragraph {
    font-size: 1.125rem;
    font-style: italic;
    margin: -58px 0 0 98px;
    font-family: 'Open Sans', sans-serif;
    color: #999;
}

.meaning-full .meaning .smith h6.head-6 {
    font-size: .9rem;
    line-height: 1.2;
    margin-top: 20px;
    margin-bottom: 20px;
    font-weight: 700;
    letter-spacing: 0;
    width: 100%;
    text-align: right;
}


@media only screen and (max-width:767px) {
    .meaning-full {
        padding: 0 0 5rem;
    }

    .meaning-full .meaning .smith h6.head-6 {
        text-align: start;
    }

    .container.meaningfull {
        max-width: 100% !important;
    }
}

@media only screen and (max-width:600px) {
    .meaning-full .meaning h6.head-6 {
        margin: 20px 0 0;
    }
}

@media only screen and (max-width:590px) {
    .meaning-full {
        padding: 0 1rem 5rem;
    }
}

@media only screen and (max-width:500px) {
    .meaning-full .meaning h2.head-2 {
        font-size: 1.75rem;
        margin-top: 20px;
        margin-bottom: 10px;
    }

    .meaning-full .meaning p.para {
        margin-top: 10px;
        margin-bottom: 10px;
    }

    .meaning-full .meaning .smith img {
        margin-bottom: 10px;
        position: static;
    }

    .meaning-full .meaning .smith p.paragraph {
        margin: 10px 0;
    }

    .meaning-full {
        grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    }
}

@media only screen and (max-width:420px) {
    .pt-5.design {
        padding-top: 0 !important;
    }

    .meaning-full {
        padding: 0 0 5rem;
    }
    .meaning-full .meaning {
        padding: 0;
    }

    .web-design .design p.paragraph {
        text-align: left;
    }

    .web-design .design-2 p.paragraph-2 {
        font-size: 1.125rem;
        line-height: 1.5;
        margin-top: 23px;
    }

    .web-design .design-2 button {
        font-size: 1rem;
        margin: 20px auto 0 0;
        padding: 11px 50px 12px 50px;
    }
    .container.maincontainerdiv {
        padding: 1rem;
    }

    .container.maincontainerdiv .row .col-md-12 p.paragraph {
        font-size: 1.125rem;
        line-height: 1.5;
        margin-top: -16px;
    }

    .meaning-full .meaning h6.head-6 {
        margin: 0;
        font-size: 1rem;
    }

    .meaning-full .meaning p.para {
        font-size: 1.125rem;
        line-height: 1.5;
        margin-top: 0px;
    }

    .meaning-full .meaning .smith img {
        margin: 0;
    }
    .meaning-full .meaning .smith p.paragraph {
        font-size: 1rem;
    }

    .meaning-full .meaning .smith h6.head-6 {
        padding: 5px 0 0 ;
    }
}