@import url('https://fonts.googleapis.com/css2?family=Merriweather:wght@300;400;700;900&family=Metrophobic&family=Open+Sans:wght@300;400;500;600;700;800&family=Roboto:wght@100;300;400;500;700;900&display=swap');

section.result {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    grid-gap: 2rem;
    padding: 3rem;
}

.result-item {
    font-family: 'Open Sans', sans-serif;
    background-color: #3575f2;
    padding: 2rem;
}

.result-item i {
    display: flex;
    color: #3575f2;
    background-color: #fff;
    width: 70px;
    height: 70px;
    border-radius: 50px;
    justify-content: center;
    align-items: center;
    font-size: 2.3rem;
}

.result-item h5.head-5 {
    font-size: 1.25rem;
    line-height: 1.2;
    margin-top: 20px;
    margin-bottom: 20px;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 2px;
    font-family: Montserrat, sans-serif;
    color: #fff;
}

.result-item p.para {
    color: white;
    margin-top: 20px;
    margin-bottom: 20px;
    font-size: 1.125rem;
    line-height: 1.8;
    font-style: italic;
}

.result-item a button {
    padding: 6px 0;
    width: 8rem;
    border: none;
    background: transparent;
    color: white;
    font-size: 1.125rem;
    text-transform: uppercase;
    border-bottom: 1px solid currentColor;
}

.result-item.even {
    position: relative;
    top: -7rem;
    background-color: #fff;
    box-shadow: 0px 0px 7px -3px black;
}

.result-item.even i {
    background-color: #3575f2;
    color: #fff;
}

.result-item.even h5.head-5 {
    color: #000;
}

.result-item.even p.para {
    color: #000;
}

.result-item.even a button {
    color: #000;
}

.result-item.orangered {
    background-color: #ff6943;
}





@media only screen and (max-width:1199px) {

    section.result {
        grid-gap: 1rem;
        padding: 3rem 0 5rem;
    }

    .anovative {
        padding-bottom: 8rem;
    }
}


@media only screen and (max-width:991px) {

    .anovative {
        padding-bottom: 0;
    }

    .result-item.even {
        top: 0;
    }

    section.result {
        grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    }
}



@media only screen and (max-width:700px) {
    section.result {
        grid-template-columns: repeat(auto-fit, minmax(500px, 1fr));
        padding: 2rem 0 4rem;
        width: 100%;
    }
    .anovative {
        margin-top: 4.5rem;
    }
}

@media only screen and (max-width:575px) {
    section.result {
        padding: 1rem 0;
    }
}

@media only screen and (max-width:546px) {
    section.result {
        grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
    }
}

@media only screen and (max-width:310px) {
    section.result {
        grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
    }
}