.timeless-container {
    background-color: rgb(242, 242, 242);
}

.container.timeless {
    padding: 4rem 7rem 5rem;
}

.container.timeless .row.row-1 {}

.container.timeless .row.row-1 .col-md-12 {}

.container.timeless .row.row-1 .col-md-12 h2.head-2 {
    font-family: Montserrat, sans-serif;
    font-size: 3rem;
    line-height: 1.1;
    margin-top: 20px;
    margin-bottom: 20px;
    font-family: Montserrat, sans-serif;
    font-weight: 700;
    text-align: left;
    color: #000 !important;
    font-family: Montserrat, sans-serif !important;
}

.container.timeless .row.row-1 .col-md-12 h2.head-2 span {
    color: #3575f2 !important;
    display: block;
}

.container.timeless .row.row-1 .col-md-12 p.para {
    font-size: 1.25rem;
    margin: 20px 152px 0px 0px;
    line-height: 1.8;
    font-family: "Open Sans", sans-serif;
    max-width: 500px;
}

.container.timeless .row.row-2 {
    padding: 3rem 0 0;
}

.container.timeless .row.row-2 .col-md-12 {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    gap: 1.5rem;
    flex-wrap: wrap;
}

.container.timeless .row.row-2 .col-md-12 .card {
    display: flex;
    flex-direction: row;
    gap: 1rem;
    width: 20rem;
    height: auto;
    border-radius: 0;
}

.container.timeless .row.row-2 .col-md-12 .card.card-1 {
    background-color: rgb(53, 117, 242);
    color: #fff;
    padding: 20px !important;
}

.container.timeless .row.row-2 .col-md-12 .card.card-2 {
    background-color: white;
    padding: 20px !important;
}

.container.timeless .row.row-2 .col-md-12 .card.card-2 h4.head-4 {
    color: rgb(53, 117, 242);
}

.container.timeless .row.row-2 .col-md-12 .card h4.head-4 {
    font-weight: 700;
}

.container.timeless .row.row-2 .col-md-12 .card p.para {
    margin-bottom: 20px;
    font-size: 0.875rem;
    line-height: 1.8;
    font-weight: 500;
    font-family: 'Open Sans', sans-serif;
}


@media only screen and (max-width:420px) {
    .container.timeless .row.row-2 .col-md-12 .card {
        width: 100%;
    }

    .container.timeless .row.row-2 .col-md-12 {
        grid-gap: .7rem;
    }
}