@import url('https://fonts.googleapis.com/css2?family=Baloo+Bhai+2:wght@400;500;600;700;800&display=swap');

.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2rem 3rem;
}

.header a.logoanchor {
    position: relative;
    z-index: 3;
}
.header i#logo {
    font-size: 2.5rem;
    color: #3d155f;
    transition: color 200ms linear;
}

.header i#burger {
    font-size: 2.5rem;
}

.header ul#list {
    list-style: none;
    padding-left: 0;
    background-color: #3d155f;
    color: #ccf381;
    padding: 3rem;
    position: absolute;
    top: 1rem;
    z-index: 1;
    transition: all 100ms linear;
    right: 33px;
    width: 30rem;
}

.header #xmark {
    position: relative;
    z-index: 2;
}

.header #xmark .line {
    border: 1px solid #3d155f;
    margin-top: 1rem;
    transition: all 150ms linear;
}

.header #xmark .line.line-1 {
    width: 40px;
    /* transform: rotate(45deg); */
    transform: rotate(0deg);
}

.header #xmark .line.line-2 {
    width: 20px;
    float: right;
    transform: rotate(0deg);
    /* transform: rotate(135deg); */
    position: relative;
    top: -5px;
}


.header ul#list #xmark {
    font-size: 3.5rem;
    float: right;
    position: relative;
    top: -5rem;
    right: -6rem;
}

.header ul#list ul.list-1 {
    list-style: none;
    padding-left: 0;
    font-size: 23px;
    line-height: 1.8;
    font-weight: 400;
    text-decoration: none;
}

.header ul#list ul.list-1 li {}

.header ul#list ul.list-1 li a {
    text-decoration: none;
    color: #ccf381;
    transition: all 100ms linear;
}

.header ul#list ul.list-1 li a:hover {
    text-decoration: underline;
    color: red;
}

.header ul#list ul.list-2 {
    list-style: none;
    padding-left: 0;
    font-size: 23px;
    margin-top: 2.5rem;
    line-height: 1.8;
    font-weight: 400;
}

.header ul#list ul.list-2 li {}

.header ul#list ul.list-2 li.hello {
    color: red;
    letter-spacing: 4px;
    text-transform: uppercase;
    width: 100%;
    font-size: 1em;
    transition: opacity 0.4s, box-shadow 0.4s;
}

.header ul#list ul.list-3 {
    list-style: none;
    padding-left: 0;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 1rem;
    margin-top: 2rem;
    font-size: 18px;
    font-weight: 400;
}

.header ul#list ul.list-3 li {}


@media only screen and (max-width:700px) {

    .header i#logo {
        font-size: 2em;
    }

    .header #xmark .line.line-1 {
        width: 40px;
    }

    .header #xmark .line.line-2 {
        width: 20px;
        top: -5px;
    }

    .header ul#list {
        font-family: 'Baloo Bhai 2', sans-serif;
        list-style: none;
        padding-left: 0;
        background-color: #3d155f;
        color: #ccf381;
        padding: 7rem 9rem;
        position: absolute;
        top: 0;
        z-index: 1;
        transition: all 100ms linear;
        right: 0;
        height: 100vh;
        width: 100%;
    }

    .header ul#list ul.list-1 {
        font-size: 21px;
        line-height: 2;
    }

    .header ul#list ul.list-2,
    .header ul#list ul.list-3 {
        font-size: 20px;
        line-height: 2;
    }

    .header ul#list ul.list-2,
    .header ul#list ul.list-3 {
        margin-top: 5rem;
    }

    .header ul#list ul.list-3 {
        gap: 4rem;
        font-size: 22px;
    }
}

@media only screen and (max-width:500px) {

    .header {
        padding: 1rem;
    }

    .header ul#list {
        padding: 7rem 3rem;
    }

    .header ul#list ul.list-3 {
        flex-wrap: wrap;
    }

    .header ul#list ul.list-2,
    .header ul#list ul.list-3 {
        margin-top: 3rem;
    }
}

@media only screen and (max-width: 415px) {
    .header ul#list ul.list-3 {
        gap: 2rem;
    }
}