
.collaborate {
    background: url('../images/background-wallpaper.jpg')no-repeat 50% 50%/cover;
    height: 90vh;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    z-index: 0;
}

.collaborate::before {
    content: '';
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.6);
}

.collaborate .container {
    color: #fff;
    position: relative;
}

.collaborate .container .row {}

.collaborate .container .row .col-md-12 {}

.collaborate .container .row .col-md-12 h1.head-1 {
    font-size: 4.5rem;
    line-height: 1.1;
    margin-top: 20px;
    margin-bottom: 30px;
    font-weight: 700;
    text-align: center;
}

.collaborate .container .row .col-md-12 h5.head-5 {
    font-size: 1.25rem;
    line-height: 1.2;
    margin-top: 20px;
    margin-bottom: 20px;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 2px;
    font-family: Montserrat, sans-serif;
    text-align: center;
}

.collaborate .container .row .col-md-12 p.freepik {
    margin-top: 20px;
    margin-bottom: 20px;
    font-size: 1.175rem;
    line-height: 1.8;
    text-align: center;
    font-weight: 600;
}

.collaborate .container .row .col-md-12 p.freepik a {
    user-select: none;
    text-decoration: none;
}


@media only screen and (max-width:500px) {

    .collaborate .container .row .col-md-12 h1.head-1 {
        font-size: 1.875rem;
    }

    .collaborate .container .row .col-md-12 h5.head-5 {
        font-size: 1.25rem;
    }

    .collaborate .container .row .col-md-12 p.freepik {
        font-size: 1.25rem;
    }

    .collaborate {
        height: 50vh;
    }
}