.images-grid-div {
    padding: 1rem 1rem 3rem;
}

.images-grid-div .images-grid-div .container {}

.images-grid-div .images-grid-div .container .row {}

.images-grid-div .container .row .col-md-12 {
    padding: 1rem 7rem;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(500px, 1fr));
    grid-gap: 2rem;
}

.images-grid-div .container .row .col-md-12 .image-div {
    height: 92vh;
    filter: brightness(0.6);
}

.images-grid-div .container .row .col-md-12 .image-div.img-div-1 {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    grid-gap: 1.5rem;
}

.images-grid-div .container .row .col-md-12 .image-div.img-div-1 .box {}

.images-grid-div .container .row .col-md-12 .image-div.img-div-1 .box.box-1 {
    background: url('../images/grid-1.jpg')no-repeat center center/cover;
}

.images-grid-div .container .row .col-md-12 .image-div.img-div-1 .box.box-2 {
    background: url('../images/grid-2.jpg')no-repeat center center/cover;
}

.images-grid-div .container .row .col-md-12 .image-div.img-div-1 .box.box-3 {
    background: url('../images/grid-3.jpg')no-repeat center center/cover;
}

.images-grid-div .container .row .col-md-12 .image-div.img-div-1 .box.box-4 {
    background: url('../images/grid-4.jpg')no-repeat center center/cover;
}

.images-grid-div .container .row .col-md-12 .image-div.img-div-2 {
    background: url('../images/grid-5.jpg')no-repeat 34% 50%/cover;
}





@media only screen and (max-width:1399px) {
    .images-grid-div .container .row .col-md-12 .image-div.img-div-1 {
        grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    }
}

@media only screen and (max-width:1199px) {
    .images-grid-div .container .row .col-md-12 .image-div {
        height: 120vh
    }

    .images-grid-div .container .row .col-md-12 .image-div.img-div-1 {
        grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    }
}

@media only screen and (max-width:991px) {
    .images-grid-div .container .row .col-md-12 {
        padding: 1rem 0;
    }

    .images-grid-div .container .row .col-md-12 .image-div.img-div-1 {
        grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    }
}

@media only screen and (max-width:767px) {
    .images-grid-div .container .row .col-md-12 .image-div {
        height: 100% !important;
    }

    .images-grid-div .container .row .col-md-12 .image-div.img-div-1 .box,
    .images-grid-div .container .row .col-md-12 .image-div.img-div-2 {
        height: 32rem !important;
    }
}

@media only screen and (max-width:572px) {
    .images-grid-div .container .row .col-md-12 .image-div.img-div-1 {
        grid-template-columns: repeat(auto-fit, minmax(239px, 1fr));
    }

    .images-grid-div .container .row .col-md-12 {
        grid-template-columns: repeat(auto-fit, minmax(239px, 1fr));
    }
}

@media only screen and (max-width:420px) {

    .images-grid-div .container .row .col-md-12 .image-div.img-div-1 .box,
    .images-grid-div .container .row .col-md-12 .image-div.img-div-2 {
        height: 25rem !important;
    }
}