@import url('https://fonts.googleapis.com/css2?family=Merriweather:wght@300;400;700;900&family=Metrophobic&family=Open+Sans:wght@300;400;500;600;700;800&family=Roboto:wght@100;300;400;500;700;900&display=swap');

/* Montserrat */
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600;700;800;900&display=swap');

h3.head-3.pt-5.text-center {
    font-family: 'Montserrat', sans-serif;
    font-weight: 700;
    font-size: 3rem;
}

.maindiv {
    width: fit-content;
}

.build-project {
    font-family: 'Open Sans', sans-serif;
    padding: 0 1rem;
}

.build-projects {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
    padding-bottom: 2rem !important;
}

.build-project h4.head-4 {
    font-size: 1.6rem;
    line-height: 1.2;
    margin-top: 20px;
    margin-bottom: 10px;
    font-weight: 700;
}

.build-project ul.list {
    list-style: none;
    padding-left: 0 !important;
}

.build-project ul.list li {
    font-size: 1.25rem;
    line-height: 1;
    margin: 20px 0 0;
    color: #353333;
    word-wrap: break-word;
    font-weight: 500;
    word-break: break-all;
}

.build-project ul.list li i {
    color: #ff6943;
}


@media only screen and (max-width:424px) {

    .build-projects {
        padding: 1rem 1rem !important;
    }

    .build-projects {
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    }

    h3.head-3.pt-5.text-center {
        font-size: 2rem;
    }
}