@import url('https://fonts.googleapis.com/css2?family=Merriweather:wght@300;400;700;900&family=Metrophobic&family=Open+Sans:wght@300;400;500;600;700;800&family=Roboto:wght@100;300;400;500;700;900&display=swap');

main {
    font-family: 'Open Sans', sans-serif;
    padding: 0 0;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
    background-color: black;
    flex-wrap: wrap;
}

main items {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    padding: 6rem 8rem;
}

main .items {
    background: url('../images/OT12YY1-min.png')no-repeat left center/cover;
}

main items div {
    background-color: #ff6943;
    width: 70px;
    height: 70px;
    border-radius: 50px;
}

main items h1.head-1 {
    font-size: 4.5rem;
    line-height: 1.1;
    margin-top: 15px;
    margin-bottom: 5px;
    font-weight: 700;
    color: white;
}

main items p.para {
    margin-top: 5px;
    margin-bottom: 5px;
    font-size: 1.125rem;
    line-height: 1.8;
    color: white;
}

main items p.para a {
    /* color: white; */
    font-weight: 600;
    text-decoration: none;
}

main items button.btn {
    background-image: none;
    border-style: none;
    letter-spacing: 2px;
    text-transform: uppercase;
    font-weight: 600;
    font-size: 1rem;
    margin: 40px auto 0 0;
    padding: 18px 50px 20px 49px;
    border-radius: 5px;
    background-color: #ff6943;
    color: white;
    transition: all 150ms linear;
}

main items button.btn:hover {
    background-color: white;
    color: black;
}



@media only screen and (max-width:818px) {

    main .items {
        height: 500px;
    }
}




@media only screen and (max-width:480px) {

    main {
        grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    }

    main items {
        padding: 6rem 2rem;
    }

    main .items {
        height: 500px;
    }

    main items h1.head-1 {
        font-size: 3.5rem;
    }
}