.ambition {
    background-color: #000;
}

.ambition .container {}

.ambition .container .row {
    color: #fff;
}

.ambition .container .row .col-md-12 {
    /* text-align: center; */
    padding: 3rem;
    font-family: 'Open Sans', sans-serif;
}

.ambition .container .row .col-md-12 h5.head-5 {
    font-size: 1.25rem;
    line-height: 1.2;
    margin-top: 20px;
    margin-bottom: 20px;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 2px;
    font-family: Montserrat, sans-serif;
}

.ambition .container .row .col-md-12 h2.head-2 {
    font-size: 3rem;
    line-height: 1.1;
    margin-top: 20px;
    margin-bottom: 20px;
    font-family: 'Open Sans', sans-serif;
    font-weight: 700;
}

.ambition .container .row .col-md-12 p.para {
    margin-top: 20px;
    margin-bottom: 20px;
    font-size: 1.125rem;
    line-height: 1.8;
    color: #7d7d7d;
    font-weight: 600;
}

.ambition .container .row .col-md-12 button {
    font-family: 'Open Sans', sans-serif;
    background-image: none;
    border-style: none;
    letter-spacing: 2px;
    text-transform: uppercase;
    font-weight: 700;
    font-size: 1rem;
    margin: 10px auto;
    padding: 18px 50px 20px 49px;
    border-radius: 5px;
    background-color: #ff6943;
    color: white;
    transition: all 150ms linear;
}

.ambition .container .row .col-md-12 button:hover {
    background-color: #ff967b;
}



@media only screen and (max-width:767px) {
    .ambition .container .row .col-md-12 h2.head-2 {
        font-size: 2.2rem;
    }

    .ambition .container .row .col-md-12 p.para {
        line-height: 1.5;
    }
    .ambition .container .row .col-md-12 {
        padding: 3rem 3px;
    }
}

@media only screen and (max-width:750px) {
    .ambition .container .row .col-md-12 {
        text-align: left;
    }
}

@media only screen and (max-width:408px) {
    .ambition .container .row .col-md-12 h2.head-2 {
        font-size: 1.9rem;
    }
}