.anovative {
    padding-bottom: 5rem;
    background-color: #fff;
    color: #000;
}

.anovative h2.head-2 {
    font-size: 3rem;
    line-height: 1.1;
    margin-top: 20px;
    margin-bottom: 20px;
    font-family: Montserrat, sans-serif;
    font-weight: 700;
    text-align: center;
}

.anovative h2.head-2 span {
    color: #3575f2 !important;
    display: block;
}

.anovative img {
    width: 100%;
}

.anovative p.para {
    text-align: center;
    font-size: 1.2rem;
    font-weight: 600;
}

.anovative p.para a {
    text-decoration: none;
}



@media only screen and (max-width:991px) {

    .anovative {
        padding-bottom: 0rem;
    }
}

@media only screen and (max-width:830px) {

    .anovative h2.head-2 {
        font-size: 2rem;
    }
}

