@import url('https://fonts.googleapis.com/css2?family=Merriweather:wght@300;400;700;900&family=Metrophobic&family=Open+Sans:wght@300;400;500;600;700;800&family=Roboto:wght@100;300;400;500;700;900&display=swap');

.container.maincontainerdiv {
    padding: 1rem 2em 8rem;
}

.container.maincontainerdiv .row {
    /* display: grid; */
    /* grid-template-columns: 702px; */
    color: #fff;
}

.container.maincontainerdiv .row .col-md-12 {
    display: flex;
    justify-content: center;
    gap: 1rem;
    flex-direction: column;
}

.container.maincontainerdiv .row .col-md-12 h2.head-2 {}

.container.maincontainerdiv .row .col-md-12 p.paragraph {
    line-height: 1.8;
    color: #fff;
    font-size: 1.25rem;
    margin: 20px auto 0 0;
    font-family: 'Open Sans', sans-serif;
}

.container.maincontainerdiv .row .col-md-12 p.freepik {
    line-height: 1.8;
    color: #fff;
    font-size: 1.25rem;
    margin: 20px auto 0 0;
    font-family: 'Open Sans', sans-serif;
    font-weight: 600;
}

.container.maincontainerdiv .row .col-md-12 p.freepik a {
    color: #ff6943;
    transition: color 150ms linear;
    text-decoration: none;
}

.container.maincontainerdiv .row .col-md-12 p.freepik a:hover {
    color: orangered ;
}



.col-md-12.itemsdiv {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    gap: 1rem;
    flex-wrap: wrap;
    padding: 2rem 2rem;
    position: relative;
    top: -13rem;
}

.col-md-12.itemsdiv .card {
    width: 26rem;
    height: 20rem;
}

.col-md-12.itemsdiv .card.card-1 {
    background-color: #fff;
}

.col-md-12.itemsdiv .card.card-2 {
    background-color: rgb(53, 117, 242);
    color: #fff;
}

.col-md-12.itemsdiv .card p.para {
    margin-bottom: 20px;
    font-size: 1.125rem;
    line-height: 1.8;
    list-icon-spacing: 0.3;
    list-icon-size: 0.8;
    font-weight: 500;
    font-family: 'Open Sans', sans-serif;
}

.col-md-22.itemsdiv .card.card-2 h4.head-4 {
    color: #fff;
}

.col-md-12.itemsdiv .card.card-2 p.para {
    color: #fff;
}


.col-md-12.itemsdiv .card.image-1 {
    background: url('../images//c42bf2c3-4442-4eff-9f56-ac81f6939f02.jpg')no-repeat center center/cover;
}

.col-md-12.itemsdiv .card.image-2 {
    background: url('../images//bf9015f8-07f4-48c4-a5f3-f979917b1c68.jpg')no-repeat center center/cover;
}

.row {
    --bs-gutter-x: 0;
}


@media only screen and (max-width:1200px) {
    .col-md-12.itemsdiv {
        top: -12rem;
    }
}

@media only screen and (max-width:991px) {
    .col-md-12.itemsdiv .card {
        width: 18rem;
    }
}

@media only screen and (max-width:767px) {
    .col-md-12.itemsdiv .card {
        width: 100%;
    }

    .container.maincontainerdiv {
        padding: 1rem 7px 8rem;
    }
}

@media only screen and (max-width:575px) {
    .container.maincontainerdiv .row .col-md-12 {
        padding: 0 37px 2rem !important;
    }
}

@media only screen and (max-width:500px) {
    .container.maincontainerdiv {
        padding: 3rem 1rem;
    }

    .col-md-12.itemsdiv {
        top: -7rem;
    }

    .container.maincontainerdiv .row .col-md-12 p.paragraph {
        line-height: 1.6;
        margin: 0;
    }

    .container.maincontainerdiv .row .col-md-12 p.freepik {
        margin: 0;
    }
}

@media only screen and (max-width:480px) {
    .container.maincontainerdiv .row .col-md-12 p.paragraph {
        font-size: 1.125rem;
        line-height: 1.5;
        margin-top: 5px;
    }

    .container.maincontainerdiv .row .col-md-12 p.freepik {
        font-size: 1.125rem;
        margin-top: 0;
    }

    .container.maincontainerdiv .row .col-md-12 {
        padding: 0 15px 2rem !important;
    }

    .container.maincontainerdiv {
        padding: 0 1rem 3rem !important;

    }
}

@media only screen and (max-width:420px) {
    .col-md-12.itemsdiv {
        padding: 2rem 0;
    }

    .container.maincontainer {
        height: 79rem !important;
    }

    .container.maincontainerdiv {
        padding: 0 0 3rem !important;
    }

    .meaning-full {
        grid-gap: 1rem !important;
        padding: 0 !important;
    }
}

@media only screen and (max-width:400px) {
    .col-md-12.itemsdiv .card {
        height: auto;
    }

    .col-md-12.itemsdiv .card.image-1,
    .col-md-12.itemsdiv .card.image-2 {
        height: 20rem;
    }

    .container.maincontainer {
        height: 69rem !important;
    }
}

@media only screen and (max-width:370px) {
    .container.maincontainer {
        height: 73rem !important;
    }
}

@media only screen and (max-width:335px) {
    .container.maincontainer {
        height: 77rem !important;
    }
}