.inovative-products {
    background-color: rgb(242, 242, 242);
}

.inovative-products .container {}

.inovative-products .container .row {}

.inovative-products .container .row .col-md-12 {
    padding: 1rem 0;
}

.inovative-products .container .row .col-md-12 h2.head-2 {
    font-family: Montserrat, sans-serif;
    font-size: 3rem;
    line-height: 1.1;
    margin-top: 20px;
    margin-bottom: 20px;
    font-family: Montserrat, sans-serif;
    font-weight: 700;
    text-align: left;
    color: #000 !important;
    font-family: Montserrat, sans-serif !important;
    text-align: center;
}

.inovative-products .container .row .col-md-12 h2.head-2 span {
    color: #3575f2 !important;
}

.inovative-products .container .row .col-md-12 .cards {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    grid-gap: 1.5rem;
    padding: 3rem;
}

.inovative-products .container .row .col-md-12 .cards .card {
    box-shadow: 0 0 10px -6px black;
    padding: 2rem 1rem;
    border-radius: 0 !important;
}

.inovative-products .container .row .col-md-12 .cards .card i {
    font-size: 2.25rem;
    background: #3575f2;
    width: 70px;
    height: 70px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    border-radius: 50px;
    margin: 0 auto;
}

.inovative-products .container .row .col-md-12 .cards h3.head-3 {
    font-weight: 700;
    font-size: 3rem;
    margin: 23px 0px 0px;
    font-family: Montserrat, sans-serif !important;
    color: rgb(53, 117, 242) !important;
    line-height: 1.2;
    word-wrap: break-word;
}

.inovative-products .container .row .col-md-12 .card h4.head-4 {
    font-size: 1.125rem;
    text-transform: uppercase;
    font-weight: 700;
    margin: 17px 0px 0px;
    font-family: Montserrat, sans-serif !important;
}



@media only screen and (max-width:1199px) {

    .inovative-products .container .row .col-md-12 .cards {
        grid-template-columns: repeat(auto-fit, minmax(270px, 1fr))
    }
}

@media only screen and (max-width:991px) {
    .inovative-products .container .row .col-md-12 h2.head-2 {
        font-size: 2.5rem;
    }
}

@media only screen and (max-width:767px) {

    .container.timeless {
        padding: 4rem 0 5rem;
    }

    .container.timeless .row.row-1 .col-md-12 h2.head-2 {
        font-size: 2rem;
    }

    .container.timeless .row.row-1 .col-md-12 p.para {
        font-size: 1.125rem;
        margin: 20px 0 0px 0px;
        line-height: 1.4;
        font-family: "Open Sans", sans-serif;
        width: 100%;
    }
}

@media only screen and (max-width:575px) {
    .container.timeless {
        padding: 4rem 1rem 5rem;
    }
}

@media only screen and (max-width:500px) {

    .container.timeless {
        padding: 0 1rem 1rem;
    }

    .inovative-products .container .row .col-md-12 h2.head-2 {
        font-size: 2rem;
    }
}

@media only screen and (max-width:500px) {

    .inovative-products .container .row .col-md-12 .cards {
        grid-template-columns: repeat(auto-fit, minmax(227px, 1fr))
    }

    .inovative-products .container .row .col-md-12 .cards {
        padding: 0;
    }

    .container.timeless .row.row-1 .col-md-12 h2.head-2 {
        font-size: 1.7rem;
    }
}

@media only screen and (max-width:480px) {
    .container.timeless .row.row-2 .col-md-12 {
        justify-content: center;
    }
}

@media only screen and (max-width:480px) {
    .container.timeless .row.row-1 .col-md-12 h2.head-2 {
        font-size: 1.4rem;
        line-height: 1.3;
    }
}

@media only screen and (max-width:420px) {
    .inovative-products .container .row .col-md-12 .cards {
        grid-gap: .6rem;
    }
}

@media only screen and (max-width:400px) {
    .inovative-products .container .row .col-md-12 h2.head-2 {
        font-size: 1.7rem;
    }
}