/* Montserrat */
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600;700;800;900&display=swap');

.form-container {
    padding: 5rem 2rem;
}

section.contact {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    grid-gap: 2rem;
    font-family: 'Montserrat', sans-serif;
    padding: 1rem;
}

section.contact .contact-us {
    background-color: #ff6943;
    padding: 5rem 7rem;
    color: white;
}

section.contact .contact-us h5.head-5 {
    font-size: 1.25rem;
    line-height: 1.2;
    margin-top: 35px;
    margin-bottom: 5px;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 2px;
    list-icon-spacing: 0.3;
    list-icon-size: 0.8;
    font-family: Montserrat, sans-serif;
}

section.contact .contact-us h5.head-5 i {
    font-size: 1.25rem;
}

section.contact .contact-us p.call {
    margin: 0;
    font-size: 1.125rem;
    line-height: 1.8;
    list-icon-spacing: 0.3;
    list-icon-size: 0.8;
    font-weight: 400;
}

form {
    background-color: white;
}

form h2.head-2 {
    font-size: 3rem;
    line-height: 1.1;
    margin-top: 20px;
    margin-bottom: 20px;
    list-icon-spacing: 0.3;
    list-icon-size: 0.8;
    font-family: Montserrat, sans-serif;
    font-weight: 700;
}

form input,
textarea {
    width: 100%;
    padding: 10px 12px;
    background-image: none;
    background-clip: padding-box;
    border-width: 0;
    border-radius: 3px;
    transition: border-color ease-in-out 0.15s, box-shadow ease-in-out 0.15s;
    background-color: transparent;
    border-bottom-width: 2px;
    margin-bottom: 25px;
    outline: none;
    border-radius: 0;
}

form textarea {}

form button {
    font-family: 'Open Sans', sans-serif;
    background-image: none;
    border-style: none;
    letter-spacing: 2px;
    text-transform: uppercase;
    font-weight: 700;
    font-size: 1rem;
    margin: 10px auto 0 0;
    padding: 18px 50px 20px 49px;
    border-radius: 5px;
    background-color: #ff6943;
    color: white;
    transition: all 150ms linear;
    float: left;
    width: 100%;
}

form button:hover {
    background-color: #000;
}



@media only screen and (max-width:1200px) {
    section.contact {
        grid-template-columns: repeat(auto-fit, minmax(100%, 1fr));
    }
}

@media only screen and (max-width:767px) {
    form button {
        font-weight: 600;
        font-size: 13px;
        padding: 9px 20px 10px 20px;
    }
}

@media only screen and (max-width:700px) {
    .container.contact {
        width: 100%;
    }

    .form-container {
        padding: 5rem 2rem !important;
    }
}

@media only screen and (max-width:500px) {
    .container.contact {
        width: 100%;
    }

    .form-container {
        padding: 2rem 0 !important;
    }

    section.contact .contact-us {
        background-color: #ff6943;
        padding: 2rem 3rem;
        color: white;
    }

    form.p-5 {
        padding: 1rem 15px !important;
    }

    form h2.head-2 {
        font-size: 2rem;
    }
}

@media only screen and (max-width:420px) {
    section.contact {
        grid-gap: 1rem;
    }
}